import React from 'react';
import View from 'components/main/pages/home/views/View';
import ImagePicker from 'components/main/items/ImagePicker';
import CustomButton from 'components/main/items/ui/CustomButton';

class Profile extends View {

  constructor(props){
    super(props);
    this.init(props);
    this.state = {
      modified: false,
      filename: this.store.profile.icon,
      type: 'profileIcon'
    }
    this.checkUrl();
  }

  render() {
    this.init(this.props);
    const profile = this.store.profile;
    const forceProfile = this.store.content.view === 'forceProfile';

    return(
      <div style={this.viewStyle()}>
        <div style={this.viewContentStyle()}>
          {this.gap(this.bs.height * 0.04)}

          {forceProfile && this.subTitle(['Please setup your profile!','!','!'], this.bs.height * 0.03)}
          {forceProfile && this.sep()}
          {forceProfile && this.gap(this.bs.height * 0.04)}

          {this.subTitle(['Your avatar','',''])}
          {this.sep()}
          <ImagePicker defaultUrl={this.url.url} app={this.app} />
          {this.sep()}
          {this.gap(this.bs.height * 0.02)}

          {this.subTitle(['Your name','',''])}
          {this.sep()}
          {this.inputs.inputField('name','text','', profile.name, ()=>{ this.setState({modified: true})})}
          {this.gap(this.bs.height * 0.02)}

          {this.subTitle(['Self introduction','',''])}
          {this.sep()}
          {this.gap(this.bs.height * 0.02)}
          {this.inputs.textArea('desc', '', profile.description, ()=>{ this.setState({modified: true})})}
          {this.gap(this.bs.height * 0.04)}

          {!forceProfile && this.lowerPart()}

          {!forceProfile && <CustomButton app={this.app} button={this.buttons.rectRed(['Confirm change','',''], ()=>{this.changing()})}/>}
          {forceProfile && <CustomButton app={this.app} button={this.buttons.rectGreen(['Confirm','',''], ()=>{this.changing()})}/>}

          {this.gap(this.bs.height * 0.08)}
        </div>
      </div>
    )
  }

  lowerPart(){
    const profile = this.store.profile;
    return(
      <div style={{...this.viewStyle(), height: this.bs.height * 0.4, flexShrink: 0}}>
        {this.subTitle(['Total submitted cards','',''])}
        {this.sep()}
        {this.textDisplay(profile.cardCount, ['50%', this.bs.height * 0.06], '150%', 'center')}
        {this.gap(this.bs.height * 0.02)}

        {this.subTitle(['Total featured cards','',''])}
        {this.sep()}
        {this.textDisplay(profile.featuredCount, ['50%',this.bs.height * 0.06], '150%', 'center')}
        {this.gap(this.bs.height * 0.02)}

        {this.subTitle(['Enter current password for any changing','',''])}
        {this.sep()}
        {this.inputs.inputField('pw','password','','')}
      </div>
    )
  }

  changing(){
    const profile = this.store.profile;
    const newIconBlob = this.store.main.photoBlob;
    const user = this.store.user;

    const newName = document.getElementById('name').value;
    const newDesc = document.getElementById('desc').value;

    //console.log(newId)
    if(!newIconBlob && !profile.icon){
      return this.failedMessage(['Failed to edit! Icon is missing!', '! !', '! !'])
    }
    if(newName === ''){
      return this.failedMessage(['Failed to change! Name is missing!', '! !', '! !'])
    }
    if(this.store.content.view !== 'forceProfile' && document.getElementById('pw').value !== user.pw){
        return this.failedMessage(['Failed to change! Please enter your password correctly!', '! !', '! !'])
    }
    if(!this.state.modified && !newIconBlob){
      return this.failedMessage(['Failed to add! Nothing is modified!', '!!', '!!'])
    }

    this.actions.profile.changeProfile({
      profile: profile,
      newName: newName,
      newDesc: newDesc,
      newIconBlob: newIconBlob
    });

  }

}

export default Profile;



