import * as reducer from '../reducer';

const langsReducer = (
  state = {
    editLangs:[],
    langs: [],
    langKeys: [
      {
        key: 'chinese_written',
        name: ['Chinese(Written)','()','()'],
        use: true
      },
      {
        key: 'english',
        name: ['English','',''],
        use: true
      },
      {
        key: 'chinese_spoken',
        name: ['Chinese(Spoken)','()','()'],
        use: true
      },
      {
        key: 'pth_written',
        name: ['PTH(Written)','()','()'],
        use: true
      },
      {
        key: 'pth_spoken',
        name: ['PTH(Spoken)','()','()'],
        use: true
      },
      {
        key: 'hindi',
        name: ['Hindi','',''],
        use: true
      },
      {
        key: 'urdu',
        name: ['Urdu','',''],
        use: true
      },
      {
        key: 'nepalese',
        name: ['Nepalese','',''],
        use: true
      },
      {
        key: 'tagalog',
        name: ['Tagalog','',''],
        use: true
      },
      {
        key: 'japanese',
        name: ['Japanese','',''],
        use: true
      },
      {
        key: 'spanish',
        name: ['Spanish','',''],
        use: true
      },
      {
        key: 'german',
        name: ['German','',''],
        use: true
      },
      {
        key: 'french',
        name: ['French','',''],
        use: true
      }
    ]
  }, action)=>{
  const newEditLangs = state.editLangs.slice(0);
  switch (action.type) {
    case 'updateLangs':
      return {...state, langs: reducer.updateElements(state.langs, action.payload)};
    case 'setLangAudio':
      const index = action.payload.index;
      newEditLangs[index] = {...newEditLangs[index], audioBlob: action.payload.blob}
      newEditLangs[action.payload.index]['edited'] = true;
      return {...state, editLangs: newEditLangs}
    case 'setEditLang':
      newEditLangs[action.payload.index] = action.payload.editLang;
      newEditLangs[action.payload.index]['edited'] = true;
      return {...state, editLangs: newEditLangs};
    case 'setEditLangs':
      return {...state, editLangs: action.payload};
    case 'killEditLangsItem':
      newEditLangs[action.payload].killed = true;
      return {...state, editLangs: newEditLangs};
    case 'removeEditLangsItem':
      newEditLangs.splice(action.payload,1);
      return {...state, editLangs: newEditLangs};
    case 'pushEditLangs':
      return {...state, editLangs: [...state.editLangs, action.payload]};
    default:
      return state;
  }
}

export default langsReducer;



